@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
header {
  position: relative;
}
header .header__contenedor {
  background-color: #262626;
  padding: 2rem;
}
header .ocultar {
  display: none !important;
}
header .header__descripcion {
  position: absolute;
  z-index: 1;
  top: 35%;
  left: 5%;
  background-color: rgba(0, 0, 0, 0.7);
  border-radius: 2rem;
  width: 30rem;
  padding: 3rem 0 3rem 2rem;
}
@media (min-width: 768px) {
  header .header__descripcion {
    top: 20rem;
    left: 15rem;
    width: 45rem;
  }
}
@media (min-width: 1024px) {
  header .header__descripcion {
    top: 25rem;
    left: 25rem;
  }
}
header .header__barra {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
header .header__enlace--principal {
  margin-right: 4rem;
  font-size: 3.7rem;
  color: #FFFFFF;
  font-weight: 900;
}
header .header__enlace--principal:hover {
  opacity: 0.5;
  cursor: pointer;
}
header .header__video {
  width: 100%;
  display: block;
}

.header__descripcion p {
  font-size: 1.5rem;
  color: #FFFFFF;
  font-weight: 900;
  margin-left: 2rem;
}
@media (min-width: 768px) {
  .header__descripcion p {
    font-size: 2.7rem;
  }
}
.header__descripcion p:before {
  content: "";
  background-color: #FFFFFF;
  width: 0.5rem;
  height: 3rem;
  display: block;
  background-repeat: no-repeat;
  rotate: y 90deg;
  position: absolute;
  left: 2rem;
  top: 9rem;
}
@media (min-width: 768px) {
  .header__descripcion p:before {
    height: 6rem;
  }
}
.header__descripcion h1 {
  font-size: 1.5rem;
  color: #e1e1e1;
}
.header__descripcion .flex {
  margin-top: 2rem;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 768px) {
  .header__descripcion .flex {
    margin-top: 0;
    flex-direction: row;
  }
}
.header__descripcion a {
  margin-right: 2rem;
}
@media (min-width: 768px) {
  .header__descripcion a {
    margin-right: 1rem;
  }
  .header__descripcion a:last-of-type {
    margin-right: 0;
  }
}

.header__enlace {
  color: #FFFFFF;
  margin-right: 2rem;
  font-size: 2rem;
  font-weight: 900;
}
.header__enlace:hover {
  opacity: 0.5;
  cursor: pointer;
}
.header__enlace:last-of-type {
  margin-right: 0;
}

.header__menu {
  display: flex;
  flex-direction: column;
}
.header__menu .mobile-menu {
  display: block;
  width: 5rem;
}
@media (min-width: 510px) {
  .header__menu .mobile-menu {
    top: 5rem;
  }
}
@media (min-width: 768px) {
  .header__menu .mobile-menu {
    display: none;
  }
}
.header__menu .mobile-menu:hover {
  cursor: pointer;
  opacity: 0.5;
}
.header__menu .header__navegacion {
  display: flex;
  flex-direction: column;
  visibility: hidden;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.7);
  padding: 2rem 1rem;
  position: absolute;
  top: 8rem;
  z-index: 2;
}
@media (min-width: 768px) {
  .header__menu .header__navegacion {
    position: inherit;
    background-color: transparent;
    padding: 0;
    flex-direction: row;
    visibility: visible;
    opacity: 1;
  }
}
.header__menu .mostrar {
  visibility: visible;
  opacity: 1;
}

.main {
  margin-top: 5rem;
}
.main .main__contenedor {
  display: grid;
  grid-auto-columns: auto;
}
@media (min-width: 768px) {
  .main .main__contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

@media (min-width: 768px) {
  .main__imagen {
    grid-column: 2/3;
  }
}

.main__contenido {
  grid-row: 2/3;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .main__contenido {
    grid-row: 1/2;
    margin-top: 5rem;
  }
}
.main__contenido .main__parrafo {
  margin: 0;
  font-size: 3rem;
  font-weight: 900;
  width: 45rem;
}

.main__lista .elemento {
  color: gray;
}

.elemento--resaltado {
  font-size: 1.5rem;
  color: #333333;
}

.section-buscador {
  margin-top: 5rem;
  background-color: #f2f3f4;
}
.section-buscador .section-buscador__contenedor {
  padding: 3rem;
}
@media (min-width: 768px) {
  .section-buscador .section-buscador__contenedor {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
.section-buscador .section-buscador__descripcion h3 {
  margin-bottom: 0;
}
.section-buscador .section-buscador__descripcion p {
  margin-top: 0.5rem;
  font-size: 1.7rem;
}

.section-buscador__buscador {
  display: flex;
  flex-direction: column;
}
@media (min-width: 480px) {
  .section-buscador__buscador {
    flex-direction: row;
  }
}

.footer {
  background-color: #262626;
  padding: 2rem;
}
.footer .footer__copyright {
  text-align: center;
  color: #FFFFFF;
  font-weight: 900;
}

.carousel__imagen {
  height: 35rem;
}

.carousel__anterior,
.carousel__siguiente {
  position: absolute;
  display: block;
  top: 18rem;
  width: 30px;
  height: 30px;
  border: none;
  cursor: pointer;
  line-height: 30px;
  text-align: center;
  background: none;
  color: #0c62fb;
  opacity: 80%;
  z-index: 1;
}

.carousel__siguiente {
  right: 1rem;
}

.carousel__anterior {
  left: -2rem;
}

.carousel__contenedor {
  position: relative;
}

.carousel__anterior:hover,
.carousel__siguiente:hover {
  opacity: 100%;
}

.carousel__lista {
  overflow: hidden;
}

.carousel__indicadores .glider-dot {
  display: block;
  width: 30px;
  height: 4px;
  background: #0c62fb;
  opacity: 0.2;
  border-radius: 0;
}

.carousel__indicadores .glider-dot:hover {
  opacity: 0.5;
}

.carousel__indicadores .glider-dot.active {
  opacity: 1;
}

.plataformas {
  margin-top: 5rem;
  background-color: #f2f3f4;
  padding: 3rem 5rem;
}

.plataformas__contenedor a {
  display: inline-block;
}
.plataformas__contenedor .plataformas__contenido {
  margin-top: 0rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.plataformas__contenedor .plataformas__contenido a {
  margin-top: 5rem;
  margin-bottom: 0;
}
.plataformas__contenedor .plataformas__logos {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .plataformas__contenedor .plataformas__logos {
    flex-direction: row;
    justify-content: space-between;
  }
}
.plataformas__contenedor .plataformas__logos i {
  margin-right: 0;
  margin-bottom: 5rem;
  font-size: 5rem;
  color: #aeaeae;
}
@media (min-width: 768px) {
  .plataformas__contenedor .plataformas__logos i {
    margin-right: 5rem;
  }
}
.plataformas__contenedor .plataformas__logos i:hover {
  color: #626262;
  cursor: pointer;
}

.webp .section {
  background-image: url("../../../build/img/fondo.webp");
}

.no-webp .section {
  background-image: url("../../../build/img/fondo.jpg");
}

.section {
  background-size: cover;
  background-repeat: no-repeat;
  height: 40rem;
  width: 100%;
  position: relative;
  margin-bottom: 15rem;
}

.section__contenido {
  background-color: #262626;
  width: 30rem;
  height: 37rem;
  padding: 3rem;
  position: absolute;
  right: 0;
  bottom: -10rem;
}
@media (min-width: 480px) {
  .section__contenido {
    width: 50rem;
  }
}
.section__contenido .section__titulo {
  color: #FFFFFF;
  font-weight: 900;
}
.section__contenido .section__titulo span {
  color: #0c62fb;
}
.section__contenido p {
  margin: 1rem;
  color: #FFFFFF;
}
.section__contenido a {
  display: inline-block;
  margin: 1rem;
  color: #FFFFFF;
  text-decoration: underline;
}

.formulario {
  background-color: #e1e1e1;
}
.formulario .formulario__contenedor {
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .formulario .formulario__contenedor {
    flex-direction: row;
  }
}

.formulario__azul {
  flex: 1;
  padding: 5rem 2rem 10rem 0rem;
  background-color: #0c62fb;
}
.formulario__azul .formulario__titulo, .formulario__azul p, .formulario__azul a {
  color: #FFFFFF;
  margin-left: 0;
  text-align: start;
}
@media (min-width: 480px) {
  .formulario__azul .formulario__titulo, .formulario__azul p, .formulario__azul a {
    margin-left: 2rem;
    padding-left: 2rem;
  }
}
.formulario__azul .resaltar-texto {
  width: 41rem;
}
.formulario__azul p {
  margin-bottom: 0;
}
.formulario__azul a {
  text-decoration: underline;
}

.formulario__form {
  padding: 2rem;
  flex: 2;
}
.formulario__form label {
  font-weight: 900;
}
.formulario__form input:not(.submit), .formulario__form select {
  display: block;
  width: 100%;
  margin-bottom: 2rem;
  border: none;
  background-color: transparent;
  border-bottom: 1px solid #333333;
}
.formulario__form .submit {
  display: inline-block;
  margin-left: 2rem;
}
.formulario__form .campo:first-of-type, .formulario__form .campo:nth-child(2) {
  margin-top: 2rem;
}
.formulario__form .texto-gris {
  margin-left: 2rem;
}

@media (min-width: 1024px) {
  .contenedor-campos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
.contenedor-campos .campo {
  margin-bottom: 4rem;
}
@media (min-width: 1024px) {
  .contenedor-campos .campo {
    margin-bottom: 0;
  }
}